import {authMe} from "./authReducer";


const INITIALIZATION_SUCCESS = "appReducer/INITIALIZATION_SUCCESS"

type InitialStateType = {
    initialized: boolean
}

let initialState: InitialStateType = {
    initialized: false
}

export const appReducer = (state = initialState, action: any): InitialStateType => {
    switch (action.type) {
        case INITIALIZATION_SUCCESS: {
            return {
                ...state,
                initialized: true,
            }
        }

        default:
            return state

    }
}

type initializationActionType = {
    type: typeof INITIALIZATION_SUCCESS
}

const initializationSuccess = (): initializationActionType => ({type: INITIALIZATION_SUCCESS})

export const initialization = () => {
    return (dispatch: any) => {
        // @ts-ignore
        let promise = dispatch(authMe())

        Promise.all([promise])
            .then(() => {
                dispatch(initializationSuccess())
            })
    }
}