import {authAPI, securityAPI} from "../API/api";

const SET_USER_LOGIN = "authReducer/SET_USER_LOGIN";
const SET_CAPTCHA_URL_SUCCESS = "authReducer/SET_CAPTCHA_URL_SUCCESS"

let initionalState = {
    userId: null as number | null,
    login: null as string | null,
    email: null as string | null,
    isAuth: null as boolean | null,
    captchaUrl: null as string | null,
};

type InitionalStateType = typeof initionalState

const authReducer = (state = initionalState, action: any): InitionalStateType  => {
    switch (action.type) {
        case SET_USER_LOGIN: {
            return {
                ...state,
                ...action.data,
            };
        }

        case SET_CAPTCHA_URL_SUCCESS: {
            return {
                ...state,
                captchaUrl: action.captchaURL,
            };
        }

        default:
            return state
    }
}

type SetUserLoginActionDataType = {
    userId: number | null
    login: string | null
    email: string | null
    isAuth: boolean
}

type SetUserLoginActionType = {
    type: typeof SET_USER_LOGIN
    data: SetUserLoginActionDataType
}

const setUserLogin = (userId: number | null, login: string | null, email: string | null, isAuth: boolean): SetUserLoginActionType => ({
        type: SET_USER_LOGIN,
        data: {userId, login, email, isAuth}
    })

type GetCaptchaUrlSuccessActionType = {
    type: typeof SET_CAPTCHA_URL_SUCCESS
    captchaURL: string
}

const getCaptchaUrlSuccess = (captchaURL: string): GetCaptchaUrlSuccessActionType => ({
    type: SET_CAPTCHA_URL_SUCCESS,
    captchaURL
})

export const authMe = (isAuth: boolean) => async (dispatch: any) => {
    let response = await authAPI.authMe()
    if (response.resultCode === 0) {
        let {id, login, email, captcha} = response.data;
        dispatch(setUserLogin(id, login, email, true))
    }
}

export const login = (loginData: any, setError: any) => async (dispatch: any) => {
    let response = await authAPI.login(loginData)

    if (response.data.resultCode === 0) {
        dispatch(authMe(true))
    } else {
        if(response.data.resultCode === 10) {
            dispatch(getCaptchaUrl())
        } else {
            setError({error: response.data.messages})
        }
    }
}



export const logout = () => async (dispatch: any) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setUserLogin(null, null, null, false))
    }
}

export const getCaptchaUrl = () => async (dispatch: any) => {
    let response = await securityAPI.getCaptchaURL()
    dispatch(getCaptchaUrlSuccess(response.url))
}

export default authReducer;